.logo{
    margin-left: auto;
    margin-right: auto;
    height: auto;
    width: auto;
    max-width: 30px;
    max-height: 30px;
    fill: #696969;
}

.logo:hover{
    margin-left: auto;
    margin-right: auto;
    height: auto;
    width: auto;
